.toggle_button{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: distribute;
        justify-content: space-around;
    height: 27px;
    width: 36px;
    background: transparent;
    border: none;
    cursor: pointer;
    padding: 0;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}
.toggle_button:focus{
    outline: none;
}
.toggle_button_line{
    width: 30px;
    height: 2px;
    background: white;
}
@media(min-width: 769px){
    .toggle_button{
        display: none;
    }
}


